:root {
	--dark2: #222831;
	--dark: #393e46;
	--aqua: #00adb5;
	--white: #eeeeee;
	--blue: #0077ff;
}

:root div {
	--toastify-color-dark: var(--dark);
	--toastify-color-info: var(--blue);
	--toastify-color-success: #1dff24;
	--toastify-color-error: #ff2912;
	--toastify-icon-color-info: var(--toastify-color-info);
	--toastify-icon-color-success: var(--toastify-color-success);
	--toastify-icon-color-error: var(--toastify-color-error);
	--toastify-toast-width: 350px;
	--toastify-toast-background: var(--white);
	--toastify-toast-min-height: 64px;
	--toastify-toast-max-height: 800px;
	--toastify-font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
		"Liberation Mono", "Courier New", monospace;
	--toastify-z-index: 9999;
	--toastify-text-color-dark: var(--white);
	--toastify-spinner-color: #616161;
	--toastify-spinner-color-empty-area: #e0e0e0;
	--toastify-color-progress-dark: var(--blue);
	--toastify-color-progress-success: var(--toastify-color-success);
	--toastify-color-progress-warning: var(--toastify-color-warning);
	--toastify-color-progress-error: var(--toastify-color-error);
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	background-color: var(--dark);
	font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
		"Liberation Mono", "Courier New", monospace;
}

pre {
	white-space: -moz-pre-wrap;
	white-space: -o-pre-wrap;
	white-space: pre-wrap;
	word-wrap: break-word;
}

