.loader {
	position: relative;
	width: 120px;
	height: 90px;
	margin: 0 auto;
}
.loader:before {
	content: "";
	position: absolute;
	bottom: 30px;
	left: 50px;
	height: 30px;
	width: 30px;
	border-radius: 50%;
	background: #0077ff;
	animation: loading-bounce 0.5s ease-in-out infinite alternate;
}
.loader:after {
	content: "";
	position: absolute;
	right: 0;
	top: 0;
	height: 7px;
	width: 45px;
	border-radius: 4px;
	box-shadow: 0 5px 0 #eeeeee, -35px 50px 0 #eeeeee, -70px 95px 0 #eeeeee;
	animation: loading-step 1s ease-in-out infinite;
}

@keyframes loading-bounce {
	0% {
		transform: scale(1, 0.7);
	}
	40% {
		transform: scale(0.8, 1.2);
	}
	60% {
		transform: scale(1, 1);
	}
	100% {
		bottom: 140px;
	}
}
@keyframes loading-step {
	0% {
		box-shadow: 0 10px 0 rgba(0, 0, 0, 0), 0 10px 0 #eeeeee,
			-35px 50px 0 #eeeeee, -70px 90px 0 #eeeeee;
	}
	100% {
		box-shadow: 0 10px 0 #eeeeee, -35px 50px 0 #eeeeee, -70px 90px 0 #eeeeee,
			-70px 90px 0 rgba(0, 0, 0, 0);
	}
}

@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
@keyframes rotationBack {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(-360deg);
	}
}

.loadersearch {
	width: 90%;
	height: 50px;
	display: block;
	background-image: linear-gradient(
			100deg,
			transparent,
			rgba(38, 50, 56, 0.2) 50%,
			transparent 80%
		),
		radial-gradient(circle 24px at 24px 25px, var(--dark) 99%, transparent 0),
		linear-gradient(var(--dark) 20px, transparent 0),
		linear-gradient(var(--dark) 20px, transparent 0);
	background-repeat: no-repeat;
	background-size: 75px 50px, 100% 100vh, 40% 40vh, 80% 40vh;
	background-position: -50% 0, 0 0, 60px 0, 60px 30px;
	animation: animloader 1s linear infinite;
}

@keyframes animloader {
	0% {
		background-position: 0% 0, 0 0, 60px 0, 60px 30px;
	}
	100% {
		background-position: 100% 0, 0 0, 60px 0, 60px 30px;
	}
}

.loadersearchdark {
	width: 90%;
	height: 200px;
	display: block;
	background-image: linear-gradient(
			100deg,
			transparent,
			rgba(57, 62, 72, 0.2) 50%,
			transparent 80%
		),
		radial-gradient(circle 24px at 24px 25px, var(--dark2) 99%, transparent 0),
		linear-gradient(var(--dark2) 20px, transparent 0),
		linear-gradient(var(--dark2) 20px, transparent 0),
		radial-gradient(circle 24px at 24px 25px, var(--dark2) 99%, transparent 0),
		linear-gradient(var(--dark2) 20px, transparent 0),
		linear-gradient(var(--dark2) 20px, transparent 0);
	background-repeat: no-repeat;
	background-size: 75px 200px, 100% 100vh, 40% 40vh, 80% 40vh, 100% 100vh,
		40% 40vh, 80% 40vh;
	background-position: -50% 0, 0 0, 60px 0px, 60px 25px, 0 70px, 60px 70px,
		60px 95px;
	animation: animloaderdark 1s linear infinite;
}

@keyframes animloaderdark {
	0% {
		background-position: 0% 0, 0 0, 60px 0px, 60px 25px, 0 70px, 60px 70px,
			60px 95px;
	}
	100% {
		background-position: 100% 0, 0 0, 60px 0px, 60px 25px, 0 70px, 60px 70px,
			60px 95px;
	}
}

.loaderavatar {
	background: linear-gradient(
			100deg,
			transparent,
			#393e467e 50%,
			transparent 80%
		),
		var(--white);
	background-repeat: no-repeat;
	background-size: 80%;
	animation: animavatar 2s infinite;
}

@keyframes animavatar {
	0% {
		background-position: -100px;
	}
	100% {
		background-position: 100px;
	}
}

.loaderchat {
	position: relative;
	width: 64px;
	height: 64px;
	background-color: rgba(34, 40, 49, 0.5);
	transform: rotate(45deg);
	overflow: hidden;
}
.loaderchat:after {
	content: "";
	position: absolute;
	inset: 8px;
	margin: auto;
	background: var(--dark);
}
.loaderchat:before {
	content: "";
	position: absolute;
	inset: -15px;
	margin: auto;
	background: var(--blue);
	animation: diamondLoader 2s linear infinite;
}
@keyframes diamondLoader {
	0%,
	10% {
		transform: translate(-64px, -64px) rotate(-45deg);
	}
	90%,
	100% {
		transform: translate(0px, 0px) rotate(-45deg);
	}
}
